.backdrop {
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

@media screen and (max-width: 1280px) {
  .mobile-menu {
    width: 250px;
    height: 100vh;
    z-index: 105;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    box-shadow: 0px 1px 5px var(--secondary-text-color-theme-light);
    background-color: #e17992;
    justify-content: center;
    align-items: flex-start;
    padding-top: 60px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translateX(0);
  }

  .mobile-menu__close-button {
    cursor: pointer;
    z-index: 105;
    color: #fff;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    line-height: 0;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .mobile-menu__close-button:hover, .mobile-menu__close-button:focus {
    color: var(--accent-color-dark);
  }

  .cls-btn {
    stroke: currentColor;
  }

  .mob__order-button {
    cursor: pointer;
    color: var(--accent-color-dark);
    font-family: var(--primary-font-family);
    min-width: 209px;
    min-height: 44px;
    background-color: #fff;
    border: none;
    border-radius: 22px;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.31;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: inline-block;
  }

  .mob__order-button:hover, .mob__order-button:focus {
    background-color: #ecebeb;
  }

  .mob__section-links {
    width: 100%;
    position: absolute;
    top: 123px;
    left: 0;
  }

  .mob__section-links-item {
    border-top: 1px solid #e18298;
  }

  .mob__section-links-item:last-child {
    border-bottom: 1px solid #e18298;
  }

  .mob__section-link {
    font-family: var(--primary-font-family);
    color: #fff;
    padding-top: 11px;
    padding-bottom: 11px;
    padding-left: 20px;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.3;
    display: block;
  }

  .mob__section-link:hover, .mob__section-link:focus {
    color: var(--accent-color-dark);
  }
}

